export const bankReducer = (state = 0, action) => {
    switch(action.type){
        case "SET_BANK":{
            return state = parseInt(action.payload * 2, 10)
        }
        case "CHANGE_BANK":{
            return state = parseInt(action.payload, 10)
        }
        default: return state;
    }
}


