import React, { Component } from 'react';
import './intro.css';

import {One, Two, Three, Four} from './intros/introcomponents.js';
import Five from './intros/introcomponents.js';

import {Switch, Route} from 'react-router-dom';

class Intro extends Component {
    constructor(props){
      super(props);

      this.state = {
        //bank: props
      }
    }
    componentDidMount(){
        document.title = 'ibac'
    }
  render() {
    return (
      <div className="App">
        <div id="Intro-bg"></div>
        <div className="Intro-container">
          <div id="Intro-contents">
            {/*<svg className="Intro-svg" viewBox="0 0 200 100"></svg>*/}
            <h1 className="App-h1">I built a <span>casino</span></h1>
            <div className="App-intro-switch">
              <Switch>
                <Route path='/intro/one' component={One} />
                <Route path='/intro/two' component={Two} />
                <Route path='/intro/three' component={Three} />
                <Route path='/intro/four' component={Four} />
                <Route path='/intro/five' component={Five} />
              </Switch>
          </div>
          <div className="App-note">
              <p className="note">
                This is not a real casino. You can't spend, win or lose any real money here.
              </p>
          </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Intro;



