import React, { Component } from 'react';

import { connect } from 'react-redux';
import { mapStateToProps, mapDispatchToProps } from './../../rdx/maps.js';

import WagerInput from './wagerinput.js';
import StartGameBtn from './startgamebtn.js';
import EndGameBtn from './endgamebtn.js';

import { formatMoney } from 'accounting';

import './roulette.css'


class Roulette extends Component {
  constructor(props){
    super(props);
    this.state = {
      bank: this.props.bank,
      rounds: this.props.rounds,
      wager: this.props.wager,
      gameStatus: 'pre-spin',
      betType: null,
      playerPick: {value: null, color: null, evenOdd: null},
      randomPick: {value: null, color: null, evenOdd: null},
      odds: 0,
      payout: 0,
      outcome: null
    }
  }

  componentWillMount(){
    this.setState({
      gameStatus: 'pre-spin'
    })

        //---------CREATES NUMBERS FROM 0 TO 36 AND ASSIGNS A 'TRUE' IF EVEN AND FALSE IF ODD------// 
    this.rouletteNumbers = [];
    for (var i = 0; i <= 36; i++){
      this.rouletteNumbers.push({
        value: i,
        evenOdd: i === 0 ? 'zero' : i % 2 === 0 ? 'even' : 'odd'
      })
    }


    //----------ASSIGNS COLORS TO EACH NUMBER---------------------------//
    this.rouletteNumbers.forEach(function(number){
      if (number.value === 0){
        number.color = 'green';
      }else if (number.value > 0 && number.value <= 11){
        number.color = number.value % 2 === 0 || number.value === 11 ? 'black' : 'red';
      }else if (number.value > 11 && number.value <= 19){
        number.color = number.value % 2 === 0 || number.value === 19 ? 'red' : 'black'; 
      }else if (number.value > 19 && number.value <= 29){
        number.color = number.value % 2 === 0 || number.value === 29 ? 'black' : 'red';
      }else{
        number.color = number.value % 2 === 0 ? 'red' : 'black';
      }
    })
  }

  componentDidMount(){
    document.title = 'ibac / ROULETTE';
    //-----------VAARIABLE TO HOLD THE NUMBER BUTTONS------------------------//
    var numberButtons;

    function getNumberButtons(){
      numberButtons = document.querySelectorAll('.roulette-numbers');
    }
    

    var that = this;

    //--------INITIAL VARIABLES-----------------//
    var betType, rouletteRandomPick, roulettePlayerPick, odds, payout, outcome;

    function initVariables(){
      betType = null;
      rouletteRandomPick = {value: null, color: null, evenOdd: null};
      roulettePlayerPick = {value: null, color: null, evenOdd: null};
      odds= 0;
      payout = 0;
      outcome = null;
    }

    initVariables();

    this.restartGame = function(){
      that.setState({
        gameStatus: 'pre-spin'
      })
      initVariables();
      that.updateState();
    }


    //---------HANDLES THE CLICK EVENT FOR DOZEN AND COLUMN CLICKS----------//
    this.handleOutsideClick = function(elem, betType){
      this.roulettePick(parseInt(elem.target.getAttribute('data-outside-index'), 10), betType, elem);
    }


    //---------SETS BET TYPE AND PLAYER'S PICK------------//
    this.roulettePick = function(pick, betTypeArg, elem){
      betType = betTypeArg;
      if(betTypeArg === 'single'){
        odds = 35;
        roulettePlayerPick = pick;
      }else if(betTypeArg === 'dozen' || betTypeArg === 'column'){
        odds = 3/4;
        if (betTypeArg === 'dozen'){
          var dozenIndex = pick;
          roulettePlayerPick = [];
          for (var i = dozenIndex; i <= dozenIndex + 11; i++){
            roulettePlayerPick.push(that.rouletteNumbers[i].value);
          }
        }
        if (betTypeArg === 'column'){
          var columnIndex = pick;
          roulettePlayerPick = [];
          for (var i = columnIndex; i <= 36; i = i + 3){
            roulettePlayerPick.push(that.rouletteNumbers[i].value);
          }
        }
      }else if(betTypeArg === 'color' || betTypeArg === 'half' || betTypeArg === 'evenOdd'){
        odds = 1/2;
        betType = pick;
        if (betTypeArg === 'half'){
          betType = betTypeArg;
          var halfIndex = pick;
          roulettePlayerPick = [];
          for (var i = halfIndex; i <= halfIndex + 17; i++){
            roulettePlayerPick.push(that.rouletteNumbers[i].value);
          }
        }
      }
      if(elem){
        changeAppearance(pick, betType, elem);
      }else{
        changeAppearance(pick, betType);
      }
      calculatePayout();
    }


    //----------
    function changeAppearance(pick, betType, elem){
      getNumberButtons();
      numberButtons.forEach(function(btn){
        btn.classList.remove('roulette-number-unselected');
        btn.classList.add('roulette-number-unselected');
      })
      if (betType === 'single'){
        var elem = elem;
        elem.target.classList.remove('roulette-number-unselected');
      }
      if (betType === 'dozen'){
        for (var i = pick; i <= pick + 11; i++){
            numberButtons[i].classList.remove('roulette-number-unselected');
        }
      }
      if (betType === 'column'){
        for (var i = pick; i <= 36; i = i + 3){
            numberButtons[i].classList.remove('roulette-number-unselected');
          }
      }
      if (betType === 'half'){
        for (var i = pick; i <= pick + 17; i++){
            numberButtons[i].classList.remove('roulette-number-unselected');
          }
      }
      if (betType === 'even' || betType === 'odd'){
          numberButtons.forEach(function(btn){
            if (btn.getAttribute('data-evenOdd') === 'even' && betType === 'even'){
              btn.classList.remove('roulette-number-unselected')
            }
            if (btn.getAttribute('data-evenOdd') === 'odd' && betType === 'odd'){
              btn.classList.remove('roulette-number-unselected')
            }
          })
      }
      if (betType === 'black' || 'red'){
        numberButtons.forEach(function(btn){
          if (btn.getAttribute('data-color') === 'black' && betType === 'black'){
            btn.classList.remove('roulette-number-unselected')
          }
          if (btn.getAttribute('data-color') === 'red' && betType === 'red'){
            btn.classList.remove('roulette-number-unselected')
          }
        })
      }
    }


    //---------CALCULATES PAYOUT--------------------------//
    function calculatePayout(){
      payout = that.state.wager * odds;
      that.updateState();
    }


    //---------HANDLES SPIN ONCLICK-----------------------//
    this.rouletteProcessSelection = function(){
      that.props.changeRounds();
      rouletteRandomPick = that.rouletteNumbers[Math.floor(Math.random() * that.rouletteNumbers.length)];
      //rouletteRandomPick = {value: 15, color: 'black', evenOdd: 'odd'}
      resolveOutcome();
      that.setState((prevState) => ({
        gameStatus: 'spinning'
      }))
    }


    //---------CHECKS IF PLAYER WON THE BET------------------------//
    function resolveOutcome(){
      if (betType === 'single'){
        resolveSingleBet();
      }else if (betType === 'dozen' || betType === 'column' || betType === 'half'){
        resolveOutsideBet();
      }else if (betType === 'red' || betType === 'black'){
        resolveColorBet();
      }else if (betType === 'even' || betType === 'odd'){
        resolveEvenOddBet();
      }
      resolvePayout();
      that.updateState();
    }


    //--------RUN THIS IF BETTYPE IS SINGLE-----------------------//
    function resolveSingleBet(){
      outcome = (roulettePlayerPick.value === rouletteRandomPick.value ? 'won' :'lost');
    }


    //--------RUN THIS IF BETTYPE IS A DOZEN OR COLUMN BET-----------------//
    function resolveOutsideBet(){
      outcome = (roulettePlayerPick.indexOf(rouletteRandomPick.value) > -1 ? 'won' : 'lost');
    }


    //--------RUN THIS IF BETTYPE IS A COLOR BET--------------------------//
    function resolveColorBet(){
      outcome = (rouletteRandomPick.color === betType ? 'won' : 'lost');
    }


    //--------RUN THIS IF BETTYPE IS EVEN OR ODD BET------------------------//
    function resolveEvenOddBet(){
      outcome = (rouletteRandomPick.evenOdd === betType ? 'won' : 'lost');
    }


    function resolvePayout(){
      if (outcome === 'won'){
        that.setState((prevState) => ({
            bank: prevState.bank + payout,
        }))
        that.props.changeBank(that.state.bank + payout)
      }else{
        that.setState((prevState) => ({
            bank: prevState.bank - that.state.wager
        }))
        that.props.changeBank(that.state.bank - that.state.wager)
      }
    }

    //--------INCREASE WAGER-----------------------------//
    this.increaseWager = function(){
        if(that.state.wager >= 8000) return;
        that.setState((prevState) => ({
            wager: prevState.wager + 200,
            payout: (prevState.wager + 200) * that.state.odds
      }))
      
    }


    //--------DECREASE WAGER-----------------------------//
    this.decreaseWager = function(){
        if(that.state.wager <= 2000) return;
        that.setState((prevState) => ({
            wager: prevState.wager - 200,
            payout: (prevState.wager - 200) * that.state.odds
      }))
    }


    //-------UPDATE STATE-------------------------------//
    this.updateState = function(){ //push to React State
      that.setState((prevState) => ({
        betType: betType,
        playerPick: roulettePlayerPick,
        randomPick: rouletteRandomPick,
        odds: odds,
        payout: payout,
        outcome: outcome
      }))
    }
    
  }

  render() {
    var that = this;
    var rouletteTableNumbers = [];
    var dozenBets = [];
    var columnBets = [];
    var otherOutsideBets = [
      <button className="outside-bet" value="even-bet" key="even-bet" onClick={() => that.roulettePick('even', 'evenOdd')}>EVEN</button>,
      <button className="outside-bet color-bet color-bet-red" value="bet-on-red" key="bet-on-red" onClick={() => that.roulettePick('red', 'color')}><div>r</div></button>,
      <button className="outside-bet color-bet color-bet-black" value="bet-on-black" key="bet-on-black" onClick={() => that.roulettePick('black', 'color')}><div>b</div></button>,
      <button className="outside-bet" value="odd-bet" key="odd-bet" onClick={() => that.roulettePick('odd', 'evenOdd')}>ODD</button>,
    ];

    var halfBets = [
      <button className="outside-bet" value="one-to-eighteen" key="one-to-eighteen" onClick={() => that.roulettePick(1, 'half')}>1to18</button>,
      <button className="outside-bet" value="nineteen-to-thirtysix" key="nineteen-to-thirtysix" onClick={() => that.roulettePick(19, 'half')}>19to36</button>
    ];
    
    this.rouletteNumbers.forEach(function(number){ //Number buttons
      rouletteTableNumbers.push(
        <button className={"roulette-number-button-"+number.color + " outsidebet roulette-numbers roulette-number-selected"} id={"roulette-number-button-"+number.value} value={number.value} 
          key={"rtNumber" + number.value} data-color={number.color} data-value={number.value} data-evenOdd={number.evenOdd}
          onClick={(elem) => that.roulettePick(number, 'single', elem)}>
            {number.value}
        </button>
      )
    })

    for (var i = 0; i < 3; i++){ //Dozen bet buttons
      dozenBets.push(
        <button className="dozen-bets outside-bet" value={(i === 0 ? 'first' : i === 1 ? 'second' : 'third') + 'twelve'} 
          key={(i === 0 ? 'first' : i === 1 ? 'second' : 'third') + '-dozen'} id={(i === 0 ? 'first' : i === 1 ? 'second' : 'third') + '-dozen'}
          data-outside-index={(i === 0 ? 1 : i === 1 ? 13 : 25)} onClick={(elem) => this.handleOutsideClick(elem, 'dozen')}> 
            {i + 1}<sup>{i === 0 ? 'st' : i === 1 ? 'nd' : 'rd'}</sup> 12
          </button>
      )
    }

    for (var i = 0; i < 3; i++){ //Column bet buttons
      columnBets.push(
        <button className="column-bets" value={(i === 0 ? 'first' : i === 1 ? 'second' : 'third') + '-column'} 
          key={(i === 0 ? 'first' : i === 1 ? 'second' : 'third') + '-column'} id={(i === 0 ? 'first' : i === 1 ? 'second' : 'third') + '-column'}
          data-outside-index={(i + 1)} onClick={(elem) => this.handleOutsideClick(elem, 'column')}> 
            2to1
          </button>
      )
    }

    return(
      <div id="roulette">
      {this.state.gameStatus === 'pre-spin' ? (
          <div id="roulette-table">
            <div id="roulette-table-bg"></div> 
            <div id="roulette-bet-table">
              {/*DIV FOR TABLE NUMBERS 0-36*/}
              <div id="roulette-table-numbers">
                {rouletteTableNumbers}
                  <div id="roulette-table-column-bets">
                    {columnBets}
                  </div>
              </div>

              {/*DIV FOR DOZEN BETS 1ST, 2ND, 3RD 12s*/}
              <div id="roulette-table-outside-bets">
                <div id="roulette-table-dozen-bets">
                  {dozenBets}
                </div>

                {/*DIV FOR THE OTHER OUTSIDE BETS*/}
                <div id="roulette-table-other-bets">
                  {otherOutsideBets}
                </div>
              </div>

              <div id='roulette-table-half-bets'>
                {halfBets}
              </div>
            </div>

            {/*DIV FOR THE TEXT PART*/}
            {this.state.betType ? (
              <div id="roulette-wager-div">
                <p>Bet Type: {this.state.betType}</p>
                <p>Odds: {this.state.odds}/1</p>
                <WagerInput bank={this.state.bank} wager={this.state.wager} increaseWager={this.increaseWager} decreaseWager={this.decreaseWager} />
                <p>If you win: <span>+{formatMoney(this.state.payout, '$', 0)}</span></p>
                <StartGameBtn bank={this.state.bank} startGameFunc={this.rouletteProcessSelection} startGameName="spin wheel"/>
            </div>
            ):(
              <div id="roulette-table-placeholder">
            </div>
            ) 
          }
            
          </div>
        ) : this.state.gameStatus === 'spinning' ? (
          <div id="roulette-wheel-div">
            <div id="roulette-wheel">
              <div id="roulette-wheel-image"></div>
              <div id="roulette-wheel-text"><p>spinning<span className="roulette-spinning-dots">.</span><span className="roulette-spinning-dots">.</span><span className="roulette-spinning-dots">.</span></p></div>
            </div>
            <div id="roulette-picked">
              <div id="roulette-picked-square" style={{backgroundColor: this.state.randomPick.color}}><p>{this.state.randomPick.value}</p></div>
              <div id="roulette-picked-outcome">
                <p>you {this.state.outcome} <span id={"roulette-picked-outcome-" + this.state.outcome}>{this.state.outcome === 'lost' ? '-' + formatMoney(this.state.wager, '$', 0) : '+' + formatMoney(this.state.payout, '$', 0)}</span></p>
                <EndGameBtn restartGame={this.restartGame}/>
              </div>
            </div>
          </div>
        ) : null}
      </div>
    )  
  }
}



export default connect(mapStateToProps, mapDispatchToProps)(Roulette);


