import React, { Component } from 'react';

import { connect } from 'react-redux';
import { mapStateToProps, mapDispatchToProps } from './../../rdx/maps.js';

import { formatMoney } from 'accounting';

import WagerInput from './wagerinput.js';
import StartGameBtn from './startgamebtn.js';
import EndGameBtn from './endgamebtn.js';

import { setSnapVariables, drawCard, rearrangeCards, clearSVG } from './bjsnapfunctions.js'

import './blackjack.css'


class Blackjack extends Component {
  constructor(props){
    super(props);
    this.state = {
      bank: this.props.bank,
      rounds: this.props.rounds,
      wager: 2400,
      dealerValue: 0,
      playerValue: 0,
      outcome: {player: 'ongoing', dealer:'ongoing'},
      payout: 0
    }
  }
  componentDidMount(){
    document.title = 'ibac / BLACKJACK';
    this.setState({gameStatus: 'prestart'});
    

    //-------GET DOM ELEMENTS----------//
    var blackjackMainBottom = document.querySelector('#blackjack-main-bottom');
    var blackjackSVG = document.querySelector('#blackjack-svg'); //The main SVG canvas
    var dealerValueText = document.querySelector('#dealer-value-text');
    var playerValueText = document.querySelector('#player-value-text');
    var svg = document.querySelector('#svgcanvas'); //The park of the svg that the cards will be drawn.

    //---------SET THE DEFAULT VARIABLES IN THE SNAPSVG FUNCTION-----------//
    setSnapVariables(svg);


    //---------FUNCTION TO CHANGE THE VIEWBOX OF MAIN SVG----//
    function changeSVGViewbox(){
      if(document.documentElement.clientWidth < 601){
        blackjackSVG.setAttribute('viewBox', '0 0 600 1067');
        dealerValueText.setAttribute('x', '300');
        dealerValueText.setAttribute('y', '370');
        dealerValueText.setAttribute('font-size', '40');
        playerValueText.setAttribute('x', '300');
        playerValueText.setAttribute('y', '420');
        playerValueText.setAttribute('font-size', '50');
      }else{
        blackjackSVG.setAttribute('viewBox', '0 0 800 450');
        dealerValueText.setAttribute('x', '400');
        dealerValueText.setAttribute('y', '200');
        dealerValueText.setAttribute('font-size', '15');
        playerValueText.setAttribute('x', '400');
        playerValueText.setAttribute('y', '220');
        playerValueText.setAttribute('font-size', '20');
      }
    }

    changeSVGViewbox();

    window.addEventListener('resize', function(event){
      changeSVGViewbox();
      rearrangeCards();
    });


    //--------FUNCTIONS TO SHOW HIDE DOM ELEMENTS----------//
    function blackjackShowBottom(){
      blackjackMainBottom.style.height = '25%';
      blackjackSVG.style.height = '75vh';
    }

    function blackjackHideBottom(){
      blackjackMainBottom.style.height = '0';
      blackjackSVG.style.height = '100vh';
    }

    this.showBlackjackReplay = function(){
      gameStatus = 'replay';
      that.updateState();
    }


    //--------INITIAL VALUES----------------//
    var that = this;
    var bank = this.state.bank;
    var dealerDecks = []; //cards drawn to the dealer goes here
    var dealerDrawn = 0; //How many cards have been drawn to the dealer
    var dealerValue = 0; //Total value of dealer's cards
    var playerDecks = []; //cards drawn to the player goes here
    var playerDrawn = 0; //How many cards have been drawn to the player
    var playerValue = 0; //Total value of player's cards
    var outcome = {};
    var gameStatus = 'prestart';
    var gameEnded = false;


    //-------CREATE DECK OF CARDS--------//
    var decks = [];
    function createDecks(){
      decks = [];
    for (var i = 1; i < 14; i++){
      decks.push({
        isFace: i > 10 ? true : false, //Anything above 10 is a face card.
        isAce: i === 1 ? true : false, //The first card is an Ace
        face: i > 10 ? ['K', 'Q', 'J'][i - 11] : i === 1 ? 'A' : i, //if isFace return face else if isAce return ace else return number
        value: i > 10 ? 10 : i, //if isFace == true value is 10, if not value is current number
        unusedsuits: ['clubs', 'diamonds', 'hearts', 'spades'],
        usedsuits: []
      })
    }
    }

    //------RESET VARIABLES TO INITIAL VALUES-----------//
    function resetValues(){
      dealerDecks = [];
      dealerDrawn = 0;
      dealerValue = 0;
      playerDecks = [];
      playerDrawn = 0;
      playerValue = 0;
      outcome = {};
      gameStatus = 'restart';
      gameEnded = false;
      blackjackHideBottom();
      that.startGame();
    }
    
    
    //---------RESETS DEFAULT VALUES AND CLEARS SVG TO RESTRT GAME------------//
    this.playAgain = function(){
      resetValues();
      clearSVG(svg);
      setSnapVariables(svg);
      that.updateState();
    }


    //--------FUNCTIONS TO SELECT CARDS FOR DEALER AND PLAYER----------//
    this.dealerDeal = function(){
      dealerDrawn++
      var cardpick = pick(); //pick a card for dealer
      dealerDecks.push(cardpick);
      that.calculateValues(); //calculate value of cards
      drawCard(svg, cardpick, 'dealer', dealerDrawn);
    }

    this.playerDeal = function(){
      playerDrawn++
      if(gameEnded){
        return false;
      }
      var cardpick = pick(); //pick a card for player
      playerDecks.push(cardpick);
      that.calculateValues(); //calculate value of cards
      drawCard(svg, cardpick, 'player', playerDrawn);
    }


    ///-------------PICKS ONE CARD FROM DECK-----------------------------//
    function pick(){
      var randNum = Math.floor(Math.random() * decks.length);
      var picked = decks[randNum]; //pick random number or face from deck
      var suitRandNum = Math.floor(Math.random() * picked.unusedsuits.length);
      var pickedsuit = picked.unusedsuits[suitRandNum]; //pick suit
      picked.usedsuits.push(picked.unusedsuits[suitRandNum]);
      picked.unusedsuits.splice(suitRandNum, 1);
      return {
        face: picked.face,
        suit: pickedsuit,
        value: picked.value,
        isAce: picked.isAce,
        color: ['spades', 'clubs'].indexOf(pickedsuit) > -1 ? '#000' : '#d40000'
      };
    }


    //---------FUNCTION TO START GAME-------------//
    this.startGame = function(){ //starts the game
      that.props.changeRounds();
      createDecks(); //creates deck of cards 
      gameStatus = 'started'; //changes game status to started
      that.updateState();
      setTimeout(function() { //waits 2ms and deals two cards each for dealer and player
        firstDeal();
      }, 200);
    }


    //--------DRAWS TWO CARDS EACH FOR DEALER AND PLAYER-----------------//
      function firstDeal(){
      while (dealerDrawn < 2){ //Draw first two cards for dealer
        that.dealerDeal();
      }
      while (playerDrawn < 2){ //Draw two cards for player
        that.playerDeal();
      }
    }


    //------ENDS GAME WHEN PLAYER REACHES OR GOES OVER 21 OR STAYS A HAND---------//
    //BUG: Does not end game if dealer gets to 21 first----//
    this.endGame = function(){
      if (gameEnded){ //if game ended is true exit this function
        return;
      }
      if(playerValue > 21){ //do the following if player goes over 21
        outcome.player = 'bust';
        outcome.dealer = 'win';
      }else if (playerValue === 21 || playerValue < 21){ //if player gets 21 or stays a hand
        if ((dealerValue < 21 && playerValue > dealerValue)){ //if dealer is under 21 and has less than the player...
          that.dealerDeal();//...draw a card for the dealer
        }
        if (dealerValue > 21){ //if dealer goes over 21
          outcome.player = 'win';
          outcome.dealer = 'bust';
        }else{
        if (playerValue < dealerValue){ //if dealer gets less than player
          outcome.player = 'lose';
          outcome.dealer = 'win';
        }
        if (playerValue > dealerValue){ //if player gets more than dealer
          outcome.player = 'win';
          outcome.dealer = 'lose';
        }
        if (playerValue === dealerValue){ //if player gets the same as dealer
          outcome.player = 'tie';
          outcome.dealer = 'tie';
        }
        }
      }
      gameEnded = true;
      gameStatus = 'ended'; //set game status to ended
      that.resolvePayout(); //calculate playout
      blackjackShowBottom(); //show bottom div.
      }


      //--------CALCULATES THE VALUES OF ALL CARDS FOR DEALER AND PLAYER------//
      //BUG: Issue with calculating aces.------//
    this.calculateValues = function(){ //self explantory
      dealerValue = 0;
      var dealerAces = []; //any ace the dealer draws goes here
      var dealerOther = []; //other cards go here
      dealerDecks.forEach(function(card){
        card.isAce ? dealerAces.push(card) : dealerOther.push(card) //separate the dealer cards
      })
      dealerOther.forEach(function(card){
        dealerValue += card.value //add up the non ace values
      })
      dealerAces.forEach(function(card){
        if((dealerValue + 11) > 21){ //if adding 11 takes the value over 21 add 1.
          dealerValue += 1
        }else{
          dealerValue += 11
        };
      })

      playerValue = 0;
      var playerAces = []; //any ace the player draws goes here
      var playerOther = [];//other cards go here
      playerDecks.forEach(function(card){
        card.isAce ? playerAces.push(card) : playerOther.push(card) //separate the player cards
      })
      playerOther.forEach(function(card){
        playerValue += card.value //add up the values of the non ace cards
      })
      playerAces.forEach(function(card){
        if((playerValue + 11) > 21){ //if adding 11 takes the value over 21 add 1.
          playerValue += 1
        }else{
          playerValue += 11
        };
      })
      if (playerValue >= 21){
        this.endGame()
      }
      this.updateState();
    }


    //-------GIVES MONEY TO OR TAKES MONEY FROM PLAYER'S BANK IN EVENT OF WIN OR LOSS-------//
    this.resolvePayout = function(){
      if(outcome.player === 'win'){
        bank += (that.state.wager * .5)
      }else if(outcome.player === 'lose' || outcome.player === 'bust'){
        bank -= that.state.wager
      }else if(outcome.player === 'tie'){
        bank += 0;
      }
      that.props.changeBank(bank);
      this.updateState();
    }



    //--------INCREASE WAGER-----------------------------//
    this.increaseWager = function(){
        if(that.state.wager >= 7000) return;
        that.setState((prevState) => ({
            wager: prevState.wager + 200
      }))
      
    }


    //--------DECREASE WAGER-----------------------------//
    this.decreaseWager = function(){
        if(that.state.wager <= 2400) return;
        that.setState((prevState) => ({
            wager: prevState.wager - 200
      }))
    }

    
    //---------UPDATES STATE-------------------------//
    this.updateState = function(){ //push to React State
      that.setState((prevState) => ({
        dealerValue: dealerValue,
        playerValue: playerValue,
        outcome: outcome,
        gameStatus: gameStatus,
        bank: bank
      }))
    }
  }
  

  render() {
    return(
      <div id="blackjack">

      {this.state.gameStatus === 'prestart' ? (
      <div id="blackjack-intro">
        <div id="blackjack-intro-text">
          <div>
          <h2>Blackjack</h2>
          <p>To win this game you'll have to get a higher score than the dealer without going over 21.</p>
          <p>Face cards are worth 10 points and number cards are worth equal to their numbers. Aces are worth either 1 or 11 depending on the situation.</p>
          <p>A win pays 1/2. A blackjack pays 3 times a win.</p>
          <WagerInput bank={this.state.bank} wager={this.state.wager} increaseWager={this.increaseWager} decreaseWager={this.decreaseWager} />
          <StartGameBtn bank={this.state.bank} startGameFunc={this.startGame} startGameName="deal" />
        </div>
        </div>
      </div>
      ): null}

        <div id="blackjack-main">
          <div>
            <svg viewBox="0 0 800 450" id="blackjack-svg">
              <text x="400" y="190" textAnchor="middle" id="dealer-value-text">Dealer: {this.state.dealerValue}</text>
              <text x="400" y="225" textAnchor="middle" id="player-value-text">Player: {this.state.playerValue}</text>
              <g id="svgcanvas"></g>
            </svg>
            {this.state.gameStatus === 'ended' || this.state.gameStatus === 'replay' ? (
              null
            ): 
              <div id="blackjack-gameplay-buttons">
                <button id="hit" onClick={this.playerDeal}>HIT</button>
                <button id="stand" onClick={this.endGame}>STAND</button>
              </div>
            }
            <div id="blackjack-main-bottom">
                {this.state.gameStatus === 'ended' ? (
                  <div id="blackjack-game-ended">
                    <div id="blackjack-game-over-div">
                      {this.state.outcome.player === 'win' ? (
                      <p>You Win <span id="blackjack-win-green">+ {formatMoney(this.state.wager * .5, '$', 0)}</span></p>
                      ): this.state.outcome.player === 'tie' ? (
                      <p>Tie + {formatMoney(0, '$', 0)}</p>
                      ): this.state.outcome.player === 'lose' || this.state.outcome.player === 'bust' ? (
                      <p>You Lose <span id="blackjack-loss-red">- {formatMoney(this.state.wager, '$', 0)}</span></p>
                    ):null}
                      <div id="blackjack-game-over-btn">
                        <EndGameBtn restartGame={this.showBlackjackReplay} />
                      </div>
                      
                    </div>
                </div>
                ): this.state.gameStatus === 'replay' ? (
                    <div id="blackjack-game-replay">
                      <WagerInput bank={this.state.bank} wager={this.state.wager} increaseWager={this.increaseWager} decreaseWager={this.decreaseWager} />
                      <StartGameBtn bank={this.state.bank} startGameFunc={this.playAgain} startGameName="deal" />
                    </div>
                ): null}
            </div>
          </div>
      </div>
      
      </div>
    )  
  }
}



export default connect(mapStateToProps, mapDispatchToProps)(Blackjack);


