import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import { connect } from 'react-redux';
import { mapStateToProps, mapDispatchToProps} from './../../rdx/maps.js';

import * as accounting from 'accounting';

class Skip extends Component {
  render() {
    return (
      <div className="skip-div">
        <Link to="/intro/five">Skip</Link>
      </div>
    );
  }
}

export class One extends Component {
  render() {
    return (
      <div>
        <p>...and before its opening I sent you and a few other lucky people a promotional offer.</p>
        <div className="links">
          <Link to="/intro/two">OK?</Link>
        </div>
        <Skip />
      </div>
    );
  }
}

export class Two extends Component {
  render() {
    return (
      <div>
        <p>I will make a 1:1 match for every dollar value chip you buy, from $5,000 to $10,000.</p>
        <div className="links">
          <Link to="/intro/three">Like...?</Link>
        </div>
        <Skip />
      </div>
    );
  }
}

export class Three extends Component {
  render() {
    return (
      <div>
        <p>Like if you buy $5,000 worth of chips, I will give you an extra $5,000 free.</p>
        <div className="links">
          <Link to="/intro/four">Free money? What's the catch.</Link>
        </div>
        <Skip />
      </div>
    );
  }
}

export class Four extends Component {
  render() {
    return (
      <div>
        <p>
        Before you can cash out your chips, you will have to play 10 rounds of games.
        <br />
        Whatever is left in the bank after the tenth round is yours to keep.
        </p>
        <div className="links">
          <Link to="/intro/five">Ok. Let's do it.</Link>
        </div>
      </div>
    );
  }
}

class Five extends Component {
  constructor(props){
    super(props);
    this.state = {
      playerCash: 0,
      bank: 0,
      rounds: 0,
      selectValue: null
    }
  }
  componentWillMount(){
    this.setState((prevState, props) => ({
      playerCash: 0,
      bank: 0,
      rounds: 0
    }))
    this.props.setPlayerCash(0);
    this.props.setBank(0);
    this.props.setRounds(0);
  }
  componentDidMount(){
    var that = this;
      var selectElement = document.querySelector('#chip-amount');
      for (var i = 0; i < 6; i++){
          var node = document.createElement('option');
          var textNode = document.createTextNode(accounting.formatMoney((5000 + 1000 * i), '$', 0));
          node.setAttribute('value', 5000 + 1000 * i)
          node.appendChild(textNode);
          selectElement.appendChild(node);
      }
      function selValue(){
        that.props.setPlayerCash(selectElement.value)
        that.props.setBank(selectElement.value)
        that.setState((prevState, props) => {
          return {selectValue: selectElement.value > 10000 ? 10000 : selectElement.value < 5000 && selectElement.value > 0 ? 5000 : selectElement.value}
        })
      }
      selectElement.onload = selValue;
      selectElement.onchange = selValue;
  }
  render() {
    var selValue = parseInt(this.state.selectValue, 10);
    var bought = accounting.formatMoney((selValue), '$', 0)
    var bankValue = accounting.formatMoney((selValue * 2), '$', 0)
    function showLink(){
      if (selValue){
        return <div>
          <p id="select-math"><span className="select-amount">{bought}</span> + <span className="select-amount">{bought}</span> = <span className="select-amount">{bankValue}</span>.</p>
            <div className="links">
              <Link className="enterLink" to="/table">Good Luck!</Link>
            </div>
          </div>
      }
        return null
    }
    return (
      <div>
        <p>Choose the amount of chips you want to buy then head in.</p>
        <select id="chip-amount">
            <option value="0">Select Amount</option>
        </select>
        {showLink()}
      </div>
    );
  }
}



export default connect(mapStateToProps, mapDispatchToProps)(Five);



