import * as snap from 'snapsvg-cjs';

import { clearSVG } from './bjsnapfunctions.js';

export function crapsDrawDots(elem, num){
    clearSVG(elem);
    var s = snap(elem);

    var dotPoints = [[75, 75],[130, 20],[20, 130],[20, 20],[130, 130],[20, 75],[130, 75]];

    for (var i = 0; i < num; i++){
        if (num % 2 === 0){
            s.circle(...dotPoints[i + 1], 20);
        }else{
            s.circle(...dotPoints[i], 20);
        }
    }
}


