import { combineReducers, createStore } from 'redux';
import { playerCashReducer } from './reducers/playercash.js';
import { bankReducer } from './reducers/bank.js';
import { roundsReducer } from './reducers/rounds.js';
import { wagerReducer } from './reducers/wager.js';

const reducers = combineReducers({
    playerCash: playerCashReducer,
    bank: bankReducer,
    rounds: roundsReducer,
    wager: wagerReducer
})

export default createStore(reducers)



