import * as snap from 'snapsvg-cjs';


function getSVG(elem){ //this function returns the SVG element
    return snap(elem)
}

var dealerCards, playerCards, dealerCardsGroup, playerCardsGroup, smScreen, lgScreen;

export function setSnapVariables(elem){ //This sets creates 
    var s = getSVG(elem);
    dealerCards = [];
    playerCards = [];
    dealerCardsGroup = s.g().attr({class: 'dealer-cards'});
    playerCardsGroup = s.g().attr({class: 'player-cards'});
    if (document.documentElement.clientWidth < 601){
        smScreen = true; //if screen is less that 601px smScreen is true...
        lgScreen = false; //...and lgScreen is false.
    }else{
        smScreen = false; //if screen is more than 600px smScreen is false...
        lgScreen = true; //...ans lgScreen is true.
    }
}

export function drawCard(elem, card, who, draws){ //passes the dom element, card picked, dealer or player, and number of draws
    var suit = card.suit,
        face = card.face,
        value = card.value,
        color = card.color;
    var imgSuitURL = process.env.PUBLIC_URL +"/"+suit+".png" //get the suit image
    var imgFaceURL = ['K', 'Q', 'J'].indexOf(face) > -1 ? process.env.PUBLIC_URL +"/"+face+suit+".png" : imgSuitURL;
    var imgPosSize = imgFaceURL === imgSuitURL ? [0, 0, 50, 50] : [-50, -95, 150, 240]; 
    var s = getSVG(elem);

    var cardboard = s.rect(0, 0, 200, 280, 5, 5) //the cardbord everyting will be printed on.
        .attr({
            fill: '#fff'
        })

        var centerSuits = s.g();
    if (imgFaceURL === imgSuitURL){ //if it is not a face card
        var onetwothreepoints = ['translate(75, 115)', 'translate(75, 25)', 'translate(125, 255) rotate(180)'];
        var fourtosevenpoints = ['translate(75, 115)', 'translate(35, 25)', 'translate(115, 25)', 'translate(165, 255) rotate(180)', 'translate(85, 255) rotate(180)', 'translate(35, 115)', 'translate(115, 115)', 'translate(75, 65)']
        var eighttotenpoints = ['translate(35, 5)', 'translate(35, 75)', 'translate(85, 195) rotate(180)', 'translate(85, 265) rotate(180)', 'translate(115, 5)', 'translate(115, 75)', 'translate(165, 195) rotate(180)', 'translate(165, 265) rotate(180)', 'translate(75, 40)', 'translate(125, 230) rotate(180)']
        if (value < 4 || value > 7 ){
            for (var i = 0; i < value; i++){
                centerSuits.append(s.image(imgFaceURL, ...imgPosSize) //the image of a face or an array of suits
                    .attr({
                        transform : value < 4 ? value !== 2 ? onetwothreepoints[i] : onetwothreepoints[i+1] : eighttotenpoints[i]
                    })
                )
            }
        }else if (value > 3 && value < 8){
            for (var i = 0; i < value; i++){
                centerSuits.append(s.image(imgFaceURL, ...imgPosSize) //the image of a face or an array of suits
                    .attr({
                        transform : value !== 5 ? fourtosevenpoints[i + 1] : fourtosevenpoints[i]
                    })
                )
            }
        }
    }else{ //if face card
        centerSuits.append(s.image(imgFaceURL, ...imgPosSize) //the image of a face or an array of suits
            .attr({
                transform : 'translate(75, 115)'
            })
        )
    }

    var faceSuit = s.g(); //suit to be on cardboard

    for (var i = 0; i < 4; i++){
        var offsets = ['0 30', '30 250', '200 250', '170 30']
        var rotate = [0, 180, 180, 0]
        faceSuit.append(s.image(imgSuitURL, 0, 0, 30, 30) //image of the suit in the bottom right corner
            .attr({
                transform : 'translate('+offsets[i]+') rotate('+rotate[i]+')'
            })
        )
    }

    var faceValue = s.g(); //number or face value of card
    
    for (var i = 0; i < 4; i++){
        var offsets = ['15 30', '15 250', '185 250', '185 30']
        var rotate = [0, 180, 180, 0]
        faceValue.append(s.text(0, 0, face) //value of the card in the top left corner
            .attr({fill: color,
                fontSize: 30,
                textAnchor: 'middle',
                letterSpacing: '-5',
                transform: 'translate('+offsets[i]+') rotate('+rotate[i]+')'
            })
        )
    }
    
    var card = s.g(cardboard, centerSuits, faceSuit, faceValue).attr({class: 'card'}); //put the card together
    if (who === 'dealer'){
        dealerCards.push(card);
    }else{
        playerCards.push(card);
    }
    
    if (document.documentElement.clientWidth < 601){
        arrangeCardsSmallScreen(card, draws, who);
    }else{
        arrangeCardsLargeScreen(card, draws, who);
    }
    
}

function arrangeCardsLargeScreen(card, draws, who){ //this function re/arranges the cards on a large screen.
    if (who === 'dealer'){ //where to place card if for dealer
        card.attr({
            transform: 'translate('+(300+draws*50)+', -300) scale(.60)'
        })
        card.animate({transform: 'translate('+(250+draws*50)+', -35) rotate(-2) scale(.60)'}, 500)
        dealerCardsGroup.append(card);
    }else{ //where to place card if for player
        card.attr({
            transform: 'translate('+(300+draws*50)+', -300) rotate(180) scale(.70)'
        })
        card.animate({transform: 'translate('+(250+draws*50)+', 280) rotate(15) scale(.70)'}, 500)
        playerCardsGroup.append(card);
    }
}

function arrangeCardsSmallScreen(card, draws, who){ //this function re/arranges the cards on a small screen.
    if (who === 'dealer'){ //where to place card if for dealer
        card.attr({
            transform: 'translate(150, -300) scale(1.4)'
        })
        card.animate({transform: 'translate(150,' + (-25 + draws * -90) +') scale(1.4)'}, 500)
        dealerCardsGroup.append(card);
    }else{ //where to place card if for player
        card.attr({
            transform: 'translate(120, -300) scale(1.5)'
        })
        card.animate({transform: 'translate(130,' + (350 + draws * 110) +') scale(1.7)'}, 500)
        playerCardsGroup.append(card);
    }
}

export function rearrangeCards(){ //function to rearrange cards when window width changes.
    if (document.documentElement.clientWidth < 601){
        if (smScreen){
            return; //if smScreen has been set to true don't run the function.
        }
        dealerCards.forEach(function(card, index){
            arrangeCardsSmallScreen(card, index + 1, 'dealer');
        })
        playerCards.forEach(function(card, index){
            arrangeCardsSmallScreen(card, index + 1, 'player');
        })
        smScreen = true; //set smScreen to true to prevent multiple rearrangements
        lgScreen = false; //set lgScreen to false so cards will rearrange when width goes above 600px.
    }else{
        if (lgScreen){
            return; //if lgScreen has been set to true don't run the function.
        }
        dealerCards.forEach(function(card, index){
            arrangeCardsLargeScreen(card, index + 1 , 'dealer');
        })
        playerCards.forEach(function(card, index){
            arrangeCardsLargeScreen(card, index + 1, 'player');
        })
        smScreen = false; //set smScreen to false so cards will rearrange when width goes below 601px.
        lgScreen = true; //set lgScreen to true to prevent multiple rearrangements.
    }
}

export function clearSVG(elem){ //clear svg for replay
    getSVG(elem).clear();
}


