export function setPlayerCash(bank){
    return {type: 'SET_PLAYER_CASH', payload: bank}
}

export function setBank(bank){
    return {type: 'SET_BANK', payload: bank}
}

export function changeBank(bank){
    return {type: 'CHANGE_BANK', payload: bank}
}

export function setRounds(round){
    return {type: 'SET_ROUNDS', payload: round}
}

export function changeRounds(round){
    return {type: 'CHANGE_ROUNDS', payload: round}
}

export function setWager(wager){
    return {type: 'SET_WAGER', payload: wager}
}


