import React, { Component } from 'react';
import {Link} from 'react-router-dom';

import { connect } from 'react-redux';
import { mapStateToProps, mapDispatchToProps} from './../../rdx/maps.js';

import * as accounting from 'accounting';

import kingofspades from './../images/kingofspades.svg';
import roulette from './../images/roulette.svg'
import dice from './../images/dice.svg';

import './tablemenu.css';

class TableMenu extends Component {
  constructor(props){
    super(props);
    this.state = {
      bank: this.props.bank,
      rounds: this.props.rounds
    }
  }
  componentDidMount(){
    document.title = 'ibac / table'
  }
  render() {
    return (
      <div className="table">
        <div className="table-info">
          <div id="table-info-bank">
            <p className="table-info-number">{accounting.formatMoney(this.state.bank, '$', 0)}</p>
            <p className="table-info-type">bank</p>
            {this.state.rounds >= 10 || this.state.bank <= 0 ? (<Link className="cash-out" id="cash-out-enabled" to="/end">cash out</Link>) : <p className="cash-out" id="cash-out-disabled" title="You have to play 10 rounds before you can cash out.">cash out</p>}
          </div>
          <div id="table-info-rounds">
            <p className="table-info-number">{this.state.rounds}</p>
            <p className="table-info-type">rounds</p>
          </div>
        </div>
        <div className="table-games">
          <Link className="table-game" to="/table/blackjack">
            <div>
              <svg className="table-blackjack-svg table-game-svg" viewBox="0 0 200 150">
                  <use xlinkHref={kingofspades + '#kingofspades'}></use>
              </svg>
              <h2>blackjack</h2>
              <p>To win this game you'll have to get a higher score than the dealer without going over 21.</p>
              <p>Face cards are worth 10 points and number cards are worth equal to their numbers. </p>
            </div>
          </Link>
          <Link className="table-game" to="/table/roulette">
            <div>
              <svg className="table-roulette-svg table-game-svg" viewBox="0 0 200 150">
                  <use xlinkHref={roulette + '#roulette'}></use>
              </svg>
              <h2>roulette</h2>
              <p>Place bets on either a single number or a range of numbers, red or black, 
              or odd or even, or if the numbers are high (1936) or low (118)</p>
            </div>
          </Link>
          <Link className="table-game" to="/table/craps">
            <div>
              <svg className="table-craps-svg table-game-svg" viewBox="0 0 200 150">
                  <use xlinkHref={dice + '#dice'}></use>
              </svg>
              <h2>craps</h2>
              <p>In this simplified version, make a bet of the outcome of the roll of a pair of dice.</p>
            </div>
          </Link>
        </div>
      </div>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(TableMenu);



