import React, { Component } from 'react';

import { formatMoney } from 'accounting';

import './wagerinput.css';



class WagerInput extends Component {
  constructor(props){
    super(props);
    this.state = {
    }
  }
  render() {
    return(
        <div className="wager-input">
              <p className="wager-text" id="wager-intro">Set your wager.</p>
              <div id="wager-input-buttons-div">
                <p className="wager-text" id="wager-number">{formatMoney(this.props.wager, '$', 0)}</p>
                <button className="wager-input-button" id='wager-input-decrease' onClick={this.props.decreaseWager}>-</button>
                <button className="wager-input-button" id='wager-input-increase' onClick={this.props.increaseWager}>+</button>
              </div>
              <p className="wager-text" id="wager-bank">bank: <span>{formatMoney(this.props.bank, '$', 0)}</span></p>
        </div>
    )
  }
}



export default WagerInput;


