import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import './endgamebtn.css'


class EndGameBtn extends Component {

  render() {
      return (
      <div>
        <Link className="return-to-table" to="/table"><button className="end-game-btn back-to-table">&lt;&lt;Back to table</button></Link>
        <button className="end-game-btn restart-game" onClick={this.props.restartGame}>play again</button>
      </div>
      )
  }
}



export default EndGameBtn;


