import React, { Component } from 'react';

import { connect } from 'react-redux';
import { mapStateToProps, mapDispatchToProps } from './../../rdx/maps.js';

import {formatMoney} from 'accounting';

import WagerInput from './wagerinput.js';
import StartGameBtn from './startgamebtn.js'
import EndGameBtn from './endgamebtn.js';

import { crapsDrawDots } from './crapssnapfunctions.js'

import './craps.css'


class Craps extends Component {
  constructor(props){
    super(props);
    this.state = {
      bank: this.props.bank,
      rounds: this.props.rounds,
      wager: this.props.wager,
      dice:{left: 1, right: 1},
      rolledDice: {left: 0, right: 0},
      outcome: null,
      odds: 5,
      payout: 0
    }
  }
  componentDidMount(){
    document.title = 'ibac / CRAPS';

    this.setState({
        gameStatus: 'preroll',
        payout: this.state.wager * this.state.odds
    });
    

    //--------GET DOM ELEMENTS----------------//
    var crapsMain = document.querySelector('#craps-main');
    var crapsDiceDiv = document.querySelector('#craps-dice-div');
    var crapsDice = document.querySelectorAll('.craps-dice')
    var crapsLeftDiceSVG = document.querySelector('#craps-left-dice-svg');
    var crapsRightDiceSVG = document.querySelector('#craps-right-dice-svg');
    var crapsRollingLeftDiceSVG = document.querySelector('#craps-rolling-left-dice-svg');
    var crapsRollingRightDiceSVG = document.querySelector('#craps-rolling-right-dice-svg');
    

    //--------INITIAL VALUES----------------//
    var that = this;
    
    var bank = this.state.bank;
    var dice = {left: 1, right: 1};
    var rolledDice = {left: 0, right: 0};
    var outcome = null;
    var gameStatus = 'preroll';
    var matchedOutcomes = [];

    //---------INITIAL LOOK OF DICE---------------//
    crapsDrawDots(crapsLeftDiceSVG, dice.left);
    crapsDrawDots(crapsRightDiceSVG, dice.right);


    //------FUNCTION TO CHANGE THE DICE VALUES-------------------//
    this.setDice = function(LorR){
        if (gameStatus !== 'preroll'){
            return;
        }
        if(LorR === 'left'){
            if(dice.left === 6){
                dice.left = 1
            }else{
                dice.left++
            }
            crapsDrawDots(crapsLeftDiceSVG, dice.left);
        }else if(LorR === 'right'){
            if(dice.right === 6){
                dice.right = 1
            }else{
                dice.right++
            }
            crapsDrawDots(crapsRightDiceSVG, dice.right);
        }
        this.updateState();
    }

    
    //---------FUNCTION TO HIDE MAIN DIV---------------------------//
    function hideCrapsMain(){
        crapsMain.style.maxHeight = '0';
        crapsMain.style.visibility = 'hidden';
    }

    //--------FUNCTION TO SHOW MAIN DIV---------------------------//
    function showCrapsMain(){
        crapsMain.style.visibility = 'visible';
        crapsMain.style.maxHeight = '500px';
    }

    
    //---------FUNCTION TO REDUCE CRAPS DICE--------------------//
    function reduceCrapsDice(){
        crapsDice.forEach(function(die){
            die.classList.remove('craps-dice-normal-size');
            die.classList.add('craps-dice-small-size');
        })
        crapsDiceDiv.classList.remove('craps-dice-div-normal-size');
        crapsDiceDiv.classList.add('craps-dice-div-small-size');
    }

    //---------FUNCTION TO INCREASE CRAPS DICE--------------------//
    function increaseCrapsDice(){
        crapsDice.forEach(function(die){
            die.classList.remove('craps-dice-small-size');
            die.classList.add('craps-dice-normal-size');
        })
        crapsDiceDiv.classList.remove('craps-dice-div-small-size');
        crapsDiceDiv.classList.add('craps-dice-div-normal-size');
    }


    //------FUNCTION TO START GAME---------------------//
    this.startGame = function(){
        that.props.changeRounds();
        gameStatus = 'rolling';
        reduceCrapsDice();
        showCrapsMain();
        window.setTimeout(function(){
            rollDice();
        }, 300);
        that.updateState();
    }


    //-------FUNCTION TO ROLL DICE----------------------//
    function rollDice(){
        var rolls = 0;
        var roll = window.setInterval(function(){
            rolledDice.left = Math.floor(Math.random() * 6) + 1;
            rolledDice.right = Math.floor(Math.random() * 6) + 1;
            crapsDrawDots(crapsRollingLeftDiceSVG, rolledDice.left);
            crapsDrawDots(crapsRollingRightDiceSVG, rolledDice.right);
            rolls++
            if (rolls > 5){
                window.clearInterval(roll);
                that.endGame();
            }
        }, 200)
    }


    //------FUNCTION TO END GAME------------------------//
    this.endGame = function(){
        gameStatus = 'rolled';
        var rolledOutcomes = [rolledDice.left, rolledDice.right];
        matchedOutcomes = [];
        if (rolledOutcomes.indexOf(dice.left) > -1){
            matchedOutcomes.push(rolledOutcomes.splice(rolledOutcomes.indexOf(dice.left), 1))
        }
        if (rolledOutcomes.indexOf(dice.right) > -1){
            matchedOutcomes.push(rolledOutcomes.splice(rolledOutcomes.indexOf(dice.right), 1))
        }
        that.updateState();
        resolvePayout();
    }


    //------FUNCTION TO RESTART GAME---------------------//
    this.restartGame = function(){
        gameStatus = 'preroll';
        increaseCrapsDice();
        hideCrapsMain();
        that.updateState();
    }


    //--------INCREASE WAGER-----------------------------//
    this.increaseWager = function(){
        if(that.state.wager >= 8000) return;
        that.setState((prevState) => ({
            wager: prevState.wager + 200,
            payout: (prevState.wager + 200) * that.state.odds
      }))
      
    }


    //--------DECREASE WAGER-----------------------------//
    this.decreaseWager = function(){
        if(that.state.wager <= 2000) return;
        that.setState((prevState) => ({
            wager: prevState.wager - 200,
            payout: (prevState.wager - 200) * that.state.odds
      }))
    }



    //-------GIVES MONEY TO OR TAKES MONEY FROM PLAYER'S BANK IN EVENT OF WIN OR LOSS-------//
    function resolvePayout(){
      if (matchedOutcomes.length !== 2){
          outcome = 'lost';
          bank -= that.state.wager;
      }else{
          outcome = 'won';
          bank += that.state.payout;
      }
      that.props.changeBank(bank);
      that.updateState();
    }

    
    //---------UPDATES STATE-------------------------//
    this.updateState = function(){ //push to React State
      that.setState((prevState) => ({
        dice: dice,
        outcome: outcome,
        gameStatus: gameStatus,
        bank: bank
      }))
    }
  }
  

  render() {
    return(
      <div id="craps">
        <div id="craps-div">
                    <div id="craps-main">
                        <div id="craps-rolling-dice-div">
                            <div id="craps-rolling-left-dice" className="craps-rolling-dice craps-dice-normal-size">
                                <svg viewBox="0 0 150 150" id="craps-rolling-left-dice-svg" className="craps-dice-svg"></svg>
                            </div>
                            <div id="craps-rolling-right-dice" className="craps-rolling-dice craps-dice-normal-size">
                                <svg viewBox="0 0 150 150" id="craps-rolling-right-dice-svg" className="craps-dice-svg"></svg>
                            </div>
                        </div>
                    </div>
                    {this.state.gameStatus === 'preroll' ? (
                        <p className="craps-text"><span className="craps-underline">Click on the dice</span> to choose an outcome to bet on.</p>)
                    :<p id="craps-your-bet">Your bet</p>}
                    <div id="craps-dice-div" className="craps-dice-div-normal-size">
                        <button onClick={() => this.setDice("left")} id="craps-left-dice" className="craps-dice craps-dice-normal-size">
                            <svg viewBox="0 0 150 150" id="craps-left-dice-svg" className="craps-dice-svg"></svg>
                        </button>
                        <button onClick={() => this.setDice("right")} id="craps-right-dice" className="craps-dice craps-dice-normal-size">
                            <svg viewBox="0 0 150 150" id="craps-right-dice-svg" className="craps-dice-svg"></svg>
                        </button>
                    </div>
                    {this.state.gameStatus === 'preroll' ? (
                        <div id="craps-wager-div">
                            <WagerInput bank={this.state.bank} wager={this.state.wager} increaseWager={this.increaseWager} decreaseWager={this.decreaseWager} />
                            <p className="craps-text" id="craps-odds">ODDS: 5/1</p>
                            <p className="craps-text" id="craps-payout">If you win: <span className="craps-outcome-win">+{formatMoney(this.state.payout, '$', 0)}</span></p>
                            <StartGameBtn bank={this.state.bank} startGameFunc={this.startGame} startGameName="roll dice"/>
                        </div>
                    ): this.state.gameStatus === 'rolled' && this.state.outcome === 'won' ? (
                        <div>
                            <p className="craps-outcome">You Win</p>
                            <p className="craps-outcome-win">+{formatMoney(this.state.payout, '$', 0)}</p>
                            <EndGameBtn restartGame={this.restartGame} />
                        </div>
                    ):this.state.gameStatus === 'rolled' && this.state.outcome === 'lost' ? (
                        <div>
                            <p className="craps-outcome">You Lost</p>
                            <p className="craps-outcome-loss">-{formatMoney(this.state.wager, '$', 0)}</p>
                            <EndGameBtn restartGame={this.restartGame} />
                        </div>
                    ):null}    
                </div>
            </div>
      
    )  
  }
}



export default connect(mapStateToProps, mapDispatchToProps)(Craps);


