import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import { connect } from 'react-redux';
import { mapStateToProps, mapDispatchToProps } from './../../rdx/maps.js';

import { formatMoney } from 'accounting';

import './end.css'


class End extends Component {
  constructor(props){
    super(props);
    this.state = {
      playerCash: this.props.playerCash,  
      bank: this.props.bank,
      rounds: this.props.rounds
    }
  }
  componentDidMount(){
    document.title = 'ibac';
  }
  

  render() {
    return(
      <div id="end">
      <div id="end-bg"></div>
      <div id="end-container">
        <div id="end-text">
          {this.state.playerCash < this.state.bank ? (
            <div>
              <p className="end-heading">Congratulations.</p>
              <p>You won {formatMoney(this.state.bank - this.state.playerCash, '$', 0)}.</p>
            </div>
          ): this.state.playerCash > this.state.bank ? (
            <div>
              <p className="end-heading">Better luck next time.</p>
              <p>You lost {formatMoney(this.state.playerCash - this.state.bank, '$', 0)}. Sorry.</p>        
            </div>
          ): (
            <div>
              <p className="end-heading">You made {formatMoney(0, '$', 0)}.</p>
              <p>Well done breaking even at a casino.</p> 
            </div>
          )}
          <Link className="return-to-casino" to="/intro">replay</Link>
        </div>
      </div>
      </div>
    )  
  }
}



export default connect(mapStateToProps, mapDispatchToProps)(End);


