import React, { Component } from 'react';
import {Switch, Route} from 'react-router-dom';

import { connect } from 'react-redux';
import { mapStateToProps, mapDispatchToProps} from './../rdx/maps.js';

import TableMenu from './table/tablemenu.js';
import Blackjack from './table/blackjack.js';
import Roulette from './table/roulette.js';
import Craps from './table/craps.js';


class Table extends Component {
  constructor(props){
    super(props);
    this.state = {
      //bank: this.props.bank,
      bank: this.props.bank,
      rounds: this.props.rounds
    }
  }
  componentDidMount(){
  }
  render() {
    return (
      <div>
          <Switch>
            <Route exact path='/table' component={TableMenu} />
            <Route path='/table/blackjack' component={Blackjack} />
            <Route path='/table/roulette' component={Roulette} />
            <Route path='/table/craps' component={Craps} />
          </Switch>
      </div>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Table);



