import { setPlayerCash, setBank, changeBank, setRounds, changeRounds, setWager } from './actions/actions.js';

export const mapStateToProps = (state) => {
    return {
        playerCash: state.playerCash,
        bank: state.bank,
        rounds: state.rounds,
        wager: state.wager
    };
};

export const mapDispatchToProps = (dispatch) => ({
  setPlayerCash(bank){
      dispatch(setPlayerCash(bank))
  },
  setBank(bank){
      dispatch(setBank(bank))
  },
  changeBank(bank){
    dispatch(changeBank(bank))
  },
  setRounds(round){
    dispatch(setRounds(round))
  },
  changeRounds(round){
    dispatch(changeRounds(round))
  },
  setWager(wager){
    dispatch(setWager(wager))
  }
})


